<template>
  <div>
    <div class="basics">
      <div style="display:flex;" v-show="BasicsList.length==0">
        <div class="avatar">
          <img class="avatarImg" src="../../assets/img/avatar.png" alt />
        </div>
        <div class="info">
          <div class="name">{{upData}}</div>
          <div class="other">
            <i style="margin-right:10px;">|</i>
            <i style="margin-right:10px;">|</i>
          </div>
        </div>
      </div>
      <div style="width:100%;" v-for="(item,i) in BasicsList" :key="i">
        <div style="display:flex;">
          <div class="avatar">
            <img v-show="item.Photo" class="avatarImg" :src="item.Photo" alt />
            <img v-show="!item.Photo" class="avatarImg" src="../../assets/img/avatar.png" alt />
          </div>
          <div class="info">
            <div
              :class="['name',item.ChineseName ? 'have' : '']"
            >{{item.ChineseName ? item.ChineseName : ""}}</div>
            <div class="other">
              <span v-show="item.Gender">{{item.Gender ? item.Gender : ""}}
              </span>
              <span v-show="item.Birthday"><i>|</i> {{item.Birthday ? item.Birthday : ""}}
              </span>
              <span v-show="item.PostAddress"><i>|</i> {{item.PostAddress ? item.PostAddress : ""}}</span>
              
            </div>
          </div>
        </div>
        <div class="contact-way">
          <div v-show="item.MobilePhone" class="phone">{{item.MobilePhone ? item.MobilePhone : "-"}}</div>
          <div v-show="item.Email" class="email">{{item.Email ? item.Email : "-"}}</div>
        </div>

        <!--            qq 
                   
                   -->
        <div class="ty" id="ty">
          <div class="conta" v-if="item.EnglishName">: {{item.EnglishName}}</div>
          <div class="conta" v-if="item.Phone">: {{item.Phone}}</div>
          <div class="conta" v-if="item.WorkStartTime">: {{item.WorkStartTime}}</div>
          <div class="conta" v-if="item.MaritalStatusID">: {{item.MaritalStatusID}}</div>
          <div class="conta" v-if="item.Child">: {{item.Child}}</div>
          <div class="conta" style="width:100%" v-if="item.Nation">: {{item.Nation}}</div>
          <div class="conta" v-if="item.IdType">: {{item.IdType}}</div>
          <div class="conta" style="width:100%" v-if="item.IdNumber">: {{item.IdNumber}}</div>
          <div class="conta" v-if="item.OverseasExperience">: {{item.OverseasExperience}}</div>
          <div class="conta" v-if="item.PoliticalStatus">: {{item.PoliticalStatus}}</div>
          <div class="conta" v-if="item.PostalCode">: {{item.PostalCode}}</div>
          <div class="conta" v-if="item.QQ">qq: {{item.QQ}}</div>
          <div class="conta" v-if="item.WeiChat">: {{item.WeiChat}}</div>
          <div class="conta" v-if="item.SalaryRangeID">: {{item.SalaryRangeID}}</div>
          <div class="conta" v-if="item.LatestEducationID">: {{item.LatestEducationID}}</div>
          <div class="conta" v-if="item.Major">: {{item.Major}}</div>
          <div class="conta" v-if="item.LatestCompany">: {{item.LatestCompany}}</div>
          <div class="conta" v-if="item.LatestPosition">: {{item.LatestPosition}}</div>
          <div class="conta" v-if="item.TotalYears">: {{item.TotalYears}}</div>
          <div class="conta" v-if="item.School">: {{item.School}}</div>
          <div class="conta" style="width:100%" v-if="item.SchoolPlace">: {{item.SchoolPlace}}</div>
          <div class="conta" v-if="item.GraduationDate">: {{item.GraduationDate}}</div>
          <div class="conta" style="width:100%" v-if="item.AdditionalInfo">: {{item.AdditionalInfo}}</div>
          <div class="conta" v-if="item.OverseaSchool">: {{item.OverseaSchool}}</div>
          <div class="conta" v-if="item.BachelorNo">: {{item.BachelorNo}}</div>
          <div class="conta" v-if="item.CollegeNo">: {{item.CollegeNo}}</div>
          <div class="conta" v-if="item.SectionOffice">: {{item.SectionOffice}}</div>
          <div class="conta" v-if="item.Hobby">: {{item.Hobby}}</div>
          <div class="conta" v-if="item.Height">: {{item.Height}}</div>
          <div class="conta" v-if="item.Weight">: {{item.Weight}}</div>
          <div class="conta" v-if="item.Nationality">: {{item.Nationality}}</div>
          <div class="conta" v-if="item.Degree">: {{item.Degree}}</div>
          <div class="conta" style="width:100%" v-if="item.BirthPlace">: {{item.BirthPlace}}</div>
          <div class="conta" style="width:100%" v-if="item.RegisteredAddress">: {{item.RegisteredAddress}}</div>
          <div class="conta" style="width:100%" v-if="item.HuKou">: {{item.HuKou}}</div>
          <div
            class="conta"
            style="width:100%"
            v-show="item.ExecutiveSummary"
          >: {{item.ExecutiveSummary}}</div>
        </div>
        <div v-show="BasicsList.length>0" class="edit" @click="toEditBasics(item)">
          <div class="icon"></div>
        </div>
      </div>
      <div v-show="BasicsList.length>0" style="width:100%;height:38px;"></div>
      <div class="add" v-show="BasicsList.length===0">
        <div class="add-link" @click="toAddBasics">
          <div class="add-icon"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "basics",
  props: ["BasicsList"],
  data() {
    return {
      data: null,
      upData:""
    };
  },
  methods: {
    toEditBasics(item) {
      let order=item.order
      this.$router.push({ 
        path:"/information",
        query:{
          upData:this.upData,
          category:"userinfo",
          companyId:sessionStorage.getItem('companyId'),
          ruipinUserId:sessionStorage.getItem('ruipinUserId'),
          templateId:sessionStorage.getItem('templateId'),
          order,
          userId:sessionStorage.getItem('userId'),
        } 
      });
    },
    toAddBasics() {
      this.$router.push({ 
        path:"/information",
        query:{
          upData:this.upData,
          category:"userinfo",
          companyId:sessionStorage.getItem('companyId'),
          ruipinUserId:sessionStorage.getItem('ruipinUserId'),
          templateId:sessionStorage.getItem('templateId'),
          order:0
        } 
      });
    }
  },
  mounted() {
  },
  components: {}
};
</script>

<style lang="scss">
.basics {
  margin-top: 17px;
  display: flex;
  flex-wrap: wrap;
  padding: 0 40px;
  position: relative;

  .avatar {
    width: 150px;
    height: 150px;
    border: 3px solid #f2f2f2;
    border-radius: 50%;

    .avatarImg {
      width: 100%;
      height: 100%;
      border-radius:50%
    }
  }
  

  .info {
    width:65%;
    font-size: 26px;
    color: #282828;
    margin-left: 40px;

    .name {
      padding: 30px 0;
      word-wrap:break-word;

      &.have {
        font-size: 40px;
        font-weight: bold;
        padding: 20px 0;
      }
    }
    .have {
      font-size: 40px;
      font-weight: bold;
      padding: 20px 0;
    }

    .other i {
      font-style: normal;
      margin: 0 6px;
      color: #bdbdbd;
      display: inline-block;
      width: 1px;
      font-size: 23px;
      line-height: 23px;
      vertical-align: 1px;
    }
  }

  .contact-way {
    font-size: 26px;
    color: #333;
    margin: 50px 0;
    line-height: 45px;
    width: 100%;

    .phone {
      background: url("../../assets/img/phone.png") left center no-repeat;
      padding-left: 43px;
      background-size: 30px 30px;
    }

    .email {
      background: url("../../assets/img/mail.png") left center no-repeat;
      padding-left: 43px;
      background-size: 30px 30px;
    }
  }
  .ty{
    width:100%;
    display: flex;
    flex-wrap: wrap;

    .conta {
      color: #666666;
      font-size: 26px;
      line-height: 45px;
      width: 50%;
    }
  }

  .edit {
    position: absolute;
    top: 16px;
    right: 20px;
  padding: 20px;

  .icon {
    width: 34px;
    height: 34px;
    background: url("../../assets/img/edit.png") center center no-repeat;
    background-size: 34px;
  }
}
  
  .add {
  width: 100%;
  // margin-top: 38px;
  display: flex;
  justify-content: center;

  .add-link {
    font-size: 28px;
    color: #587cec;
    display: flex;
    align-items: center;
    padding: 35px;

    .add-icon {
      width: 36px;
      height: 36px;
      background: url("../../assets/img/add.png") center center no-repeat;
      background-size: contain;
      margin-right: 10px;
    }
  }
  }
}
</style>


